@import url("https://fonts.googleapis.com/css2?family=Inter:wght@400;900&display=swap");
body {
  margin: 0;
  line-height: normal;
}
:root {
  /* fonts */
  --font-inter: Inter;

  /* font sizes */
  --font-size-base: 1rem;
  --font-size-smi: 0.81rem;

  /* Colors */
  --color-gray-100: rgba(0, 0, 0, 0.7);
  --color-gainsboro: #e6e6e6;
  --color-white: #fff;
  --color-black: #000;

  /* border radiuses */
  --br-xl: 20px;
}
